                        let ctl = model.controls[entity.objectId][state.index];
                        delete model.controls[entity.objectId][state.index];
                        if (ctl && ctl.objectId) {
                            aP.push(new Promise(function(res, rej) {
                                am7client.delete("CONTROL", ctl.objectId, function(s, v) {
                                    res();
                                });
                            }));
                        }
